import { Flex } from "components/Flex";
import { Heading, Image, Pane, Text, minorScale } from "evergreen-ui";
import { useState } from "react";
import { Col, Row } from "react-flexbox-grid";
import { useTranslation } from "react-i18next";
import { motion } from "framer-motion";
import { wrap } from "popmotion";
import styled from "styled-components";

export default function IntroductionPage() {
  const { t } = useTranslation();

  return (
    <>
      <Flex paddingX={minorScale(8)} paddingY={minorScale(6)}>
        <Text>{t("introduction.welcome")}</Text>

        <Flex.Middle>
          <Carousel items={Items} />
        </Flex.Middle>

        <Flex.SafeArea />
      </Flex>
    </>
  );
}

const Items = [
  {
    heading: "notifications.1",
    picture: "/assets/notifications/1.svg",
  },
  {
    heading: "notifications.2",
    picture: "/assets/notifications/2.svg",
  },
];

function Carousel({ items }: { items: typeof Items }) {
  const { t } = useTranslation();
  const [[page, direction], setPage] = useState([0, 0]);

  const imageIndex = wrap(0, items.length, page);

  const paginate = (newDirection: number) => {
    setPage([page + newDirection, newDirection]);
  };

  return (
    <>
      <motion.div
        key={page}
        custom={direction}
        variants={variants}
        initial="enter"
        animate="center"
        exit="exit"
        transition={{
          x: { type: "spring", stiffness: 300, damping: 30 },
          opacity: { duration: 0.2 },
        }}
        drag="x"
        dragConstraints={{ left: 0, right: 0 }}
        dragElastic={1}
        onDragEnd={(e, { offset, velocity }) => {
          const swipe = swipePower(offset.x, velocity.x);

          if (swipe < -swipeConfidenceThreshold) {
            paginate(1);
          } else if (swipe > swipeConfidenceThreshold) {
            paginate(-1);
          }
        }}
      >
        <Heading size={700}>{t(items[imageIndex].heading)}</Heading>
        <Pane marginY={minorScale(12)}>
          <Row center="xs">
            <Image src={items[imageIndex].picture} />
          </Row>
        </Pane>
      </motion.div>
      <Row center="xs">
        {new Array(items.length).fill(0).map((_, index) => (
          <Col key={index}>
            <Pane
              marginX="4px"
              borderRadius="4px"
              background={
                index === imageIndex ? "colors.main90" : "colors.black80"
              }
            >
              <Dot
                animate={index === imageIndex ? { width: 32 } : { width: 8 }}
              />
            </Pane>
          </Col>
        ))}
      </Row>
    </>
  );
}

const variants = {
  enter: (direction: number) => {
    return {
      x: direction > 0 ? 1000 : -1000,
      opacity: 0,
    };
  },
  center: {
    zIndex: 1,
    x: 0,
    opacity: 1,
  },
  exit: (direction: number) => {
    return {
      zIndex: 0,
      x: direction < 0 ? 1000 : -1000,
      opacity: 0,
    };
  },
};

const swipeConfidenceThreshold = 10000;
const swipePower = (offset: number, velocity: number) => {
  return Math.abs(offset) * velocity;
};

const Dot = styled(motion.div)`
  height: 8px;
`;
